<template>
  <div class="education">
    <el-card v-for="(item,index) in title" :key="index" class="card">
       <el-breadcrumb separator=">" class="bread">
        <el-breadcrumb-item :to="{ path: '/about' }"
          ><i class="el-icon-location-outline"></i>首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>孕育知识</el-breadcrumb-item>
        <el-breadcrumb-item>{{item}}</el-breadcrumb-item>
      </el-breadcrumb>
    </el-card>
     <el-card class="right">
      <el-row>
        <h3 class="margin-bottom right-title">反馈信息</h3>
      </el-row>
      <el-row>
        <el-form
          label-position="left"
          label-width="100px"
          :model="feedback"
        >
          <el-form-item label="意见反馈:">
            <el-input v-model="feedback.problem" placeholder="请详细描述您的反馈意见" type="textarea"  :rows="6"></el-input>
          </el-form-item>

        </el-form>
        <el-button  class="btn" round>提交</el-button>

      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  data(){
    return {
      title: ['产前护理','产后护理','婴儿护养'],
      feedback:{
        problem: ''
      }
    }
  }
}
</script>

<style scoped>
.education{
  width: 1400px;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;

}
.card{
  height: 200px;
  width: 1000px;
  margin: 20px;
}
.right{
  position: fixed;
  width: 360px;
  top: 200px;
  right: 50px;
  text-align: center;
}
.right-title{
  color: #000;
  font-weight: bold;
  font-size: 20px;
}
.btn{
  background-color: #f40;
  width: 80%;
  color: #fff;
}
</style>


