<template>
  <el-row class="classify">
    <el-col :span="3" class="title">{{title}}</el-col>
    <el-col :span="2" v-for="(item,index) in content" :key="index" class="text" @click.native="getCurrent(index)"><a href="#" :class="{active: index === currentIndex}" >{{item}}</a></el-col>
  </el-row>
</template>

<script>
export default {
  props: {
    title: String,
    content: Array
  },
  data(){
    return {
      currentIndex: 0
    }
  },
  methods: {
    getCurrent(index){
      this.currentIndex = index;
      console.log(index);
    }
  }
}
</script>

<style scoped>
.classify{
  /* background-color: pink; */
  line-height: 40px;
  border: 1px solid rgba(204, 204, 204, 0.8);
  border-bottom: none;
  box-sizing: content-box;
  padding: 5px;
  transition: height, 2s;
}
.title{
  background-color: #e9e7ef;
  opacity: 0.8;
  text-align: center;
}
.text{
  text-align: center;
  font-size: 14px;
 
}
a{
  color: #666;
  padding:  3px 8px;
  border: 1px solid transparent;
}
a:hover{
  border: 1px solid red;
  color: pink;
}
.active{
  background-color: #f40;
}


</style>


