<template>
  <div class="discuss">
    <el-card class="left">
      <el-breadcrumb separator=">" class="bread">
        <el-breadcrumb-item :to="{ path: '/about' }"
          ><i class="el-icon-location-outline"></i>首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>社区交流</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="problem-box" v-for="(item, index) in problem" :key="index">
        <el-row class="title-box" @click.native="show = !show">
          <el-col :span="20"
            ><span>{{ item.title }}</span></el-col
          >
          <el-col :span="4"><i class="el-icon-arrow-right"></i></el-col>
        </el-row>
      </div>
    </el-card>
    <el-card class="right">
      <el-row>
        <h3 class="margin-bottom right-title">发起/回复话题</h3>
      </el-row>
      <el-row>
        <el-form
          label-position="left"
          label-width="100px"
          :model="problemForm"
        >
          <el-form-item label="问题标题:">
            <el-input v-model="problemForm.title" placeholder="请输入问题标题"></el-input>
          </el-form-item>
          <el-form-item label="您所在的社区:">
            <el-input v-model="problemForm.address" placeholder="请输入您所在的社区"></el-input>
          </el-form-item>
          <el-form-item label="具体问题:">
            <el-input v-model="problemForm.problem" placeholder="请详细描述您的问题" type="textarea"  :rows="6"></el-input>
          </el-form-item>

        </el-form>
        <el-button  class="btn" round>发布话题</el-button>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      problem: [
        {
          title: "1.问题1",
        },
        {
          title: "2.问题2",
        },
        {
          title: "3.问题3",
        },
        {
          title: "4.问题4",
        },
        {
          title: "5.问题5",
        },
        {
          title: "6.问题6",
        },
        {
          title: "7.问题7",
        },
        {
          title: "8.问题8",
        },
      ],
      problemForm: {
        title: '',
        address: '',
        problem: ''
      }
    };
  },

  methods: {
    handleChange() {},
  },
};
</script>

<style scoped>
.discuss {
  width: 1400px;
  margin: auto;
  display: flex;
}
.left {
  width: 1000px;
}
.bread {
  margin-bottom: 30px;
}
.problem-box {
  display: flex;
}
.title-box {
  margin: 20px;
  width: 100%;
  cursor: pointer;
  font-size: 20px;
}
.title-box:hover {
  color: #38f;
}
.right {
  width: 360px;
  margin-left: 40px;
  text-align: center;
}
.right-title{
  color: #000;
  font-weight: bold;
  font-size: 24px;
}
.btn{
  background-color: #f40;
  width: 80%;
  color: #fff;
}
</style>


