<template>
  <div class="center">
    <el-card class="cart">
      <el-breadcrumb separator=">" class="bread">
        <el-breadcrumb-item @click.native="remove" :to="{ path: '/about' }"
          ><i class="el-icon-location-outline"></i>首页</el-breadcrumb-item
        >
        <el-breadcrumb-item>月子中心</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="classify-box">
        <classify :title="'所在省份'" :content="content"></classify>
        <classify
          :title="'价格区间'"
          :content="[
            '不限',
            '5千-1万',
            '1万-2万',
            '2-5万',
            '5万-10万',
            '10万以上',
          ]"
        ></classify>
        <classify
          :title="'类型'"
          :content="['不限', '酒店式', '公寓式', '别墅式', '医院式', '独栋式']"
        ></classify>
      </div>
      <el-menu
        :default-active="activeIndex1"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        background-color="#f5f5f5"
        text-color="#000"
        active-text-color="#f50"
      >
        <el-menu-item index="1">综合</el-menu-item>
        <el-menu-item index="2">销量</el-menu-item>
        <el-menu-item index="3">信用</el-menu-item>
        <el-submenu index="4" >
          <template slot="title">{{price}}</template>
          <el-menu-item index="4-1">从高到低</el-menu-item>
          <el-menu-item index="4-2">从低到高</el-menu-item>
        </el-submenu>
        
      </el-menu>

      <div class="shop-box" @click="jok">
        <img src="../../assets/img/shop01.png" alt="">
        <img src="../../assets/img/shop01.png" alt="">
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: ["不限","北京","上海","天津","河北","内蒙古","河北","山西","辽宁","吉林",],
      contentCount: 0,
      activeIndex1: '1',
      price: '价格'
    };
  },
  methods: {
    handleSelect(index){
      if(index === '4-1'){
        this.price = '价格从高到低'
      }else if(index === '4-2'){
          this.price = '价格从低到高'
      }
    },
    remove(){
      localStorage.clear('index');
    },
    jok(){
      this.$message.warning('尊敬的客户，很抱歉！！！ 这只是一个预览作品，没有真正的数据。');
    }
  },
};
</script>

<style scoped>
.center {
  position: relative;
  width: 1400px;
  margin: auto;
  display: flex;
  justify-content: space-between;
}
.bread {
  font-size: 16px;
}

.cart {
  width: 100%;
}
.problem {
  margin: 20px 0;
  width: 300px;
  height: 100%;
  font-size: 18px;
  color: #000;
}
.problem:hover{
  color: #f40;
  cursor: pointer;
}
.classify-box {
  margin-top: 20px;
  border-bottom: 1px solid #ccc;
}
.icon-box {
  position: absolute;
  left: 940px;
  top: 70px;
  color: #f40;
  font-size: 30px;
  cursor: pointer;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.icon-box:hover {
  color: #dc3023;
}
.el-menu-demo{
  margin: 20px 0;
  border: 1px solid #e8e8e8;
}
.shop-box{
  display: flex;
  flex-direction: column;
}
.shop-box img{
  padding: 20px;
  border: 1px solid #ccc;
  border-bottom: none ;
  cursor: pointer;
}
</style>


